import React from 'react';
import { TranslateButton as Button } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
;
import { Controller } from '@ali/4ever-cangjie';

export interface ItranslateButtonProps {
  locale: any;
  config?: any;
  size?: string;
  showTitle?: boolean;
  controller: Controller;
}

export const TranslateButton: React.FC<ItranslateButtonProps> = (props) => {
  const { locale, config, controller, ...rest } = props;
  const { triggerTranslateSideMenu = () => {} } = config;
  const handleClick = React.useCallback(() => {
    triggerTranslateSideMenu();
  }, [controller]);
  const disabled = controller.query('isSelectionInListSymbol');
  return (
    // @ts-ignore
    <Button
      {...rest}
      onClick={handleClick}
      disabled={disabled}
      role={PluginRoles.translate}
    />
  );
};

export const renderButton = (config) => {
  return <TranslateButton {...config} />;
};
